import React from 'react';
import { Form, Input, Button, } from 'antd';
import { Link } from "react-router-dom";
import {withFirebase} from '../utils/Firebase';


const INITIAL_STATE = {
    email: '',
    error: null,
  };
class PasswordForget extends React.Component {
    constructor(props) {
        super(props);
    
        this.state = { ...INITIAL_STATE };
      }
    
      onSubmit = event => {
        const { email } = this.state;
    
        this.props.firebase
          .doPasswordReset(email)
          .then(() => {
            this.setState({ ...INITIAL_STATE });
          })
          .catch(error => {
            this.setState({ error });
          });
    
        event.preventDefault();
      };
    
      onChange = event => {
        this.setState({ [event.target.name]: event.target.value });
      };
    render() {
        const { email, error } = this.state;

        const isInvalid = email === '';
        return (
            <>
                <Form id="registerForm" onSubmit={this.onSubmit} className="login-form">
                    <Form.Item>
                        <Input
                            name="email"
                            value={this.state.email}
                            onChange={this.onChange}
                            type="text"
                            placeholder="Địa chỉ email"
                        />
                    </Form.Item>
                    <Form.Item>
                        {/* <Checkbox>Remember me</Checkbox> */}
                        
                        <Button type="primary" htmlType="submit" className="login-form-button" disabled={isInvalid} >
                            ok
                        </Button>
                        {error && <p>{error.message}</p>}
                        <Link to={'/login'}>Đăng nhập</Link>
                        <Link to={'/register'} className="login-form-forgot">Đăng ký</Link>
                        
                    </Form.Item>
                </Form>
            </>
        );
    }
}

export default withFirebase(PasswordForget);