import React from "react";
import { Col, Card, Typography, Spin } from "antd";
import firebase from "firebase";
import * as utils from "../utils/utils";
import { Link } from "react-router-dom";

const { Meta } = Card;
const { Text } = Typography;
export default class CardPost extends React.Component {
  state = {
    isLoading: true,
    dataSource: []
  };

  constructor(props) {
    super(props);
    firebase
      .database()
      .ref("posts")
      .once("value", snapshot => {
        const data = [];
        snapshot.forEach(function(childSnapshot) {
          data.push({
            ...childSnapshot.val(),
            key: childSnapshot.key
          });
          // ...
        });
        this.setState({ dataSource: data, isLoading: false });
      });
  }

  render() {
    if (this.state.isLoading) {
      return (
        <div className="loadData">
          <Spin size="large" tip="Loading..." />
        </div>
      );
    }
    return (
      <>
        {this.state.dataSource.map(item => {
          if (item.status === "published") {
            return (
              <Col key={item.key} className="gutter-row" span={6}>
                <Link to={"/viewPost?id=" + item.key}>
                  <Card
                    className="cardPost"
                    extra={
                      <span className="card-post__category">{item.tags}</span>
                    }
                    cover={<img alt="example" src={item.link} />}
                  >
                    <Meta
                      style={{ marginBottom: 8 }}
                      title={item.title}
                      description={item.description}
                    />
                    <Text code>
                      {utils.convertTimeToString(
                        item.date,
                        "Ngày đăng: DD/MM/YYYY"
                      )}
                    </Text>
                  </Card>
                </Link>
              </Col>
            );
          }
        })}
      </>
    );
  }
}
