import React from 'react';
import { Form, Input, Button, } from 'antd';
import { Link } from "react-router-dom";
import {withFirebase} from '../utils/Firebase';


const INITIAL_STATE = {
    username: '',
    email: '',
    passwordOne: '',
    passwordTwo: '',
    isAdmin: false,
    error: null,
  };
  
  const ERROR_CODE_ACCOUNT_EXISTS = 'auth/email-already-in-use';
  
  const ERROR_MSG_ACCOUNT_EXISTS = `
    An account with this E-Mail address already exists.
    Try to login with this account instead. If you think the
    account is already used from one of the social logins, try
    to sign in with one of them. Afterward, associate your accounts
    on your personal account page.
  `;
class Register extends React.Component {
    constructor(props) {
        super(props);
    
        this.state = { ...INITIAL_STATE };
      }
    
      onSubmit = event => {
        const { username, email, passwordOne, isAdmin } = this.state;
        const roles = {};
    
        if (isAdmin) {
          roles['ADMIN'] = 'ADMIN';
        }
    
        this.props.firebase
          .doCreateUserWithEmailAndPassword(email, passwordOne)
          .then(authUser => {
            // Create a user in your Firebase realtime database
            return this.props.firebase.user(authUser.user.uid).set({
              username,
              email,
              roles,
            });
          })
          .then(() => {
            return this.props.firebase.doSendEmailVerification();
          })
          .then(() => {
            this.setState({ ...INITIAL_STATE });
            this.props.history.push('/');
          })
          .catch(error => {
            if (error.code === ERROR_CODE_ACCOUNT_EXISTS) {
              error.message = ERROR_MSG_ACCOUNT_EXISTS;
            }
    
            this.setState({ error });
          });
    
        event.preventDefault();
      };
    
      onChange = event => {
        this.setState({ [event.target.name]: event.target.value });
      };
    
      onChangeCheckbox = event => {
        this.setState({ [event.target.name]: event.target.checked });
      };

      handleConfirmBlur = e => {
        const { value } = e.target;
        this.setState({ confirmDirty: this.state.confirmDirty || !!value });
      };
    
      compareToFirstPassword = (rule, value, callback) => {
        const { form } = this.props;
        if (value && value !== form.getFieldValue('password')) {
          callback('Hai mật khẩu không giống nhau!');
        } else {
          callback();
        }
      };
    
      validateToNextPassword = (rule, value, callback) => {
        const { form } = this.props;
        if (value && this.state.confirmDirty) {
          form.validateFields(['confirm'], { force: true });
        }
        callback();
      };
    render() {
        const {
            email,
            passwordOne,
            passwordTwo,
            error,
          } = this.state;

          const isInvalid =
          passwordOne !== passwordTwo ||
          passwordOne === '' ||
          email === '';

          
          const tailFormItemLayout = {
            wrapperCol: {
              xs: {
                span: 24,
                offset: 0,
              },
              sm: {
                span: 16,
                offset: 4,
              },
            },
          };
          const { getFieldDecorator } = this.props.form;
        return (
            <>
                <Form id="registerForm" onSubmit={this.onSubmit} className="login-form">
                    <Form.Item label="Email">
                    {getFieldDecorator('email', {
                        rules: [
                        {
                            type: 'email',
                            message: 'Địa chỉ E-mail không hợp lệ!',
                        },
                        {
                            required: true,
                            message: 'Vui lòng nhập địa chỉ E-mail của bạn!',
                        },
                        ],
                    })(<Input
                        name="email"
                        value={email}
                        onChange={this.onChange}
                        type="text"
                        placeholder="Email Address"
                    />)}
                        
                    </Form.Item>
                    <Form.Item label="Password" hasFeedback>
                    {getFieldDecorator('password', {
                        rules: [
                        {
                            required: true,
                            message: 'Vui lòng nhập mật khẩu của bạn!',
                        },
                        {
                            validator: this.validateToNextPassword,
                        },
                        ],
                    })(<Input.Password 
                        name="passwordOne"
                        value={passwordOne}
                        onChange={this.onChange}
                        type="password"
                        placeholder="Password"
                    />)}
                        
                    </Form.Item>
                    <Form.Item label="Confirm Password" hasFeedback>
                    {getFieldDecorator('confirm', {
                        rules: [
                        {
                            required: true,
                            message: 'Vui lòng xác nhận mật khẩu của bạn!',
                        },
                        {
                            validator: this.compareToFirstPassword,
                        },
                        ],
                    })(<Input.Password 
                        name="passwordTwo"
                        value={passwordTwo}
                        onChange={this.onChange}
                        type="password"
                        placeholder="Confirm Password"
                        onBlur={this.handleConfirmBlur}
                    />)}
                        
                    </Form.Item>
                    <Form.Item {...tailFormItemLayout}>
                        {/* <Checkbox>Remember me</Checkbox> */}
                        
                        <Button type="primary" htmlType="submit" className="login-form-button" disabled={isInvalid} >
                            Đăng ký
                        </Button>
                        {error && <p>{error.message}</p>}
                        <Link to={'/login'}>Đăng nhập</Link>
                        <Link to={'/passwordForget'} className="login-form-forgot">Quên mật khẩu</Link>
                    </Form.Item>
                </Form>
            </>
        );
    }
}

export default withFirebase(Form.create({ name: 'register' })(Register));