import React from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from "react-router-dom";
import Authorizations from './layouts/Authorizations';
import Login from './pages/Login';
import MainLayout from './layouts/MainLayout';
import EditorPage from './pages/Editor';
import Table from './pages/Table';
import {withFirebase} from './utils/Firebase'
import Register from './pages/Register';
import PasswordForget from './pages/PasswordForget';
import BlogPosts from './pages/BlogPosts';
import ViewPosts from './pages/ViewPosts';

class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      isLogin: false,
      isLoading: true,
    };
    this.listener = this.props.firebase.onAuthUserListener(
      authUser => {
        console.log('login success 1');
        this.setState({ isLogin: true, isLoading: false });
      },
      () => {
        console.log('login fail');
        this.setState({ isLogin: false, isLoading: false });
      },
    );
  }

  componentDidMount(){
    
  }

  componentWillUnmount(){
    if(this.listener){
      this.listener();
    }
  }
  render(){
    if(this.state.isLoading){
      return <div className="preloader">
                <div className="status"></div>
              </div>
    }
    return(
    <Router>
      {!this.state.isLogin?
      <Authorizations>
        <Switch>
          <Route path='/login' component={Login} />
          <Route path='/register' component={Register} />
          <Route path='/passwordForget' component={PasswordForget} />
          <Redirect to={'/login'}></Redirect>
          
        </Switch>
      </Authorizations>:
      <MainLayout>
        <Switch>
          <Route exact path='/' component={Table} />
          <Route exact path='/editorPage' component={EditorPage} />    
          <Route exact path='/blogPost' component={BlogPosts} /> 
          <Route exact path='/viewPost' component={ViewPosts} />       
          <Redirect to={'/'}></Redirect>
        </Switch>
      </MainLayout>}
    </Router >
    )
  }
  
};


export default withFirebase(App);