import { Upload, Icon, message, Modal, Button, Row, Col } from 'antd';
import React from "react";
import axios from 'axios';
import firebase from "firebase";

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}
export default class UploadImg extends React.Component {
  state = {
    link: '',
    previewVisible: false,
    previewImage: '',
    fileList: [

    ],
    file: {}
  }
  //   constructor(props) {
  //     super(props);
  //     this.state = { file: null }
  // }

  //   handleChange = (event) => {
  //       const file = event.target.files[0];
  //       this.setState({ file });
  //   }

  updateLink = (link) => {this.setState({fileList: [{
    
      uid: '1',
      name: 'image.png',
      status: 'done',
      url: link,
    
  }]})}

  handleCancel = () => this.setState({ previewVisible: false });

  handlePreview = async file => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }

    this.setState({
      previewImage: file.url || file.preview,
      previewVisible: true,
    });
  };

  handleChange = (event) => {
    console.log(event);
    this.setState({ file: event.file, fileList: event.fileList })
  }
  uploadToFirebase = () => {
    const metadata = {
      contentType: 'image/jpeg'
    };
    const storageRef = firebase.storage().ref();
    console.log(this.state.file)
    const uploadTask = storageRef.child('images/' + this.state.file.name).put(this.state.file.originFileObj, metadata);
    // Listen for state changes, errors, and completion of the upload.
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
       (snapshot) => {
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        
        console.log('Upload is ' + Math.floor(progress) + '% done');
        message.info('Tải lên là ' + Math.floor(progress) + '% hoàn thành');
        switch (snapshot.state) {
          case firebase.storage.TaskState.PAUSED: // or 'paused'
            console.log('Upload is paused');
            message.warning('Tải lên bị dừng lại')
            break;
          case firebase.storage.TaskState.RUNNING: // or 'running'
            console.log('Upload is running');
            // message.info('Đang tải')
            break;
        }
      }, function (error) {

        // A full list of error codes is available at
        // https://firebase.google.com/docs/storage/web/handle-errors
        switch (error.code) {
          case 'storage/unauthorized':
            // User doesn't have permission to access the object
            break;

          case 'storage/canceled':
            // User canceled the upload
            break;


          case 'storage/unknown':
            // Unknown error occurred, inspect error.serverResponse
            break;
        }
      },  () => {
        // Upload completed successfully, now we can get the download URL
        uploadTask.snapshot.ref.getDownloadURL().then( (downloadURL) => {
          this.props.onUploadSuccess(downloadURL);
          // console.log('File available at', downloadURL);
        });
      });
  }


  render() {
    const { previewVisible, previewImage, fileList } = this.state;
    const uploadForm = (

      <div className="dragger">
        <p className="ant-upload-drag-icon">
          <Icon type="inbox" />
        </p>
        <p className="ant-upload-text">Nhấp hoặc kéo hình vào khu vực này để tải lên</p>
        <p className="ant-upload-hint">
          Chọn 1 tấm hình làm ảnh đại diện cho bài viết.
                </p>
      </div>
    );
    const uploadButton = (
      <Button type="primary" style={{marginLeft: 20}} shape="round" icon="upload" size="large" onClick={this.uploadToFirebase}>
          Đăng hình lên
        </Button>
    )

    return (
      <>

        <Row gutter={[{ xs: 8, sm: 16, md: 24, lg: 32 }, 20]}>
          <Col span={24}>
            <Upload
              className="uploadImg"
              action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
              listType="picture-card"
              fileList={fileList}
              onPreview={this.handlePreview}
              onChange={this.handleChange}
              
            >
              {fileList.length >= 1 ? null : uploadForm}
              
            </Upload>
          </Col>
          <Col span={24}>
            {fileList.length >=1 ? uploadButton : null}
          </Col>
          <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
            <img alt="example" style={{ width: '100%' }} src={previewImage} />
          </Modal>
          
        </Row>
       
      </>
    );
  }
} 