import React from "react";
import { Table, Input, Button, Icon, Tag, Popconfirm, Divider, Spin, Typography} from "antd";
import Highlighter from "react-highlight-words";
import { Link } from "react-router-dom";
import firebase from 'firebase';
import * as utils from '../utils/utils'


const { Title } = Typography;
export default class Tables extends React.Component {
  state = {
    loading: false,
    iconLoading: false,
    searchText: "",
    searchedColumn: "",
    isLoading: true,
    dataSource: [
    ]
  };

  constructor(props) {
    super(props);
    firebase.database().ref('posts').once('value', snapshot => {
      const data = [];
      snapshot.forEach(function (childSnapshot) {
        data.unshift({
          ...childSnapshot.val(),
          key: childSnapshot.key
        });
        // ...
      });
      this.setState({ dataSource: data, isLoading: false });
      console.log('view', data)
    });
  }

  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      clearFilters
    }) => (
        <div style={{ padding: 8 }}>
          <Input
            ref={node => {
              this.searchInput = node;
            }}
            placeholder={`Search ${dataIndex}`}
            value={selectedKeys[0]}
            onChange={e =>
              setSelectedKeys(e.target.value ? [e.target.value] : [])
            }
            onPressEnter={() =>
              this.handleSearch(selectedKeys, confirm, dataIndex)
            }
            style={{ width: 188, marginBottom: 8, display: "block" }}
          />
          <Button
            type="primary"
            onClick={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
            icon="search"
            size="small"
            style={{ width: 90, marginRight: 8 }}
          >
            Search
        </Button>
          <Button
            onClick={() => this.handleReset(clearFilters)}
            size="small"
            style={{ width: 90 }}
          >
            Reset
        </Button>
        </div>
      ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? "#1890ff" : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select());
      }
    },
    render: text =>
      this.state.searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: "#ffc069", padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text.toString()}
        />
      ) : (
          text
        )
  });

  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex
    });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: "" });
  };

  handleDelete = key => {
    const dataSource = [...this.state.dataSource];
    this.setState({ dataSource: dataSource.filter(item => item.key !== key) });
    firebase.database().ref('posts/' + key).remove()
    
  };
  render() {
    
    const columns = [
      {
        title: "Thời gian đăng",
        dataIndex: "date",
        key: "date",
        ...this.getColumnSearchProps("date"),
        render: text => <span>{utils.convertTimeToString(text, "DD/MM/YYYY hh:mm")}</span>
        
      },
      {
        title: "Tiêu đề",
        dataIndex: "title",
        key: "title",
        ...this.getColumnSearchProps("title")
      },
      {
        title: "Tác giả",
        dataIndex: "author",
        key: "author",
        ...this.getColumnSearchProps("author")
      },
      {
        title: "Lượt xem",
        dataIndex: "countView",
        key: "countView",
        ...this.getColumnSearchProps("countView"),
        render: countView => {
          return (
            <Title level={4}>{countView}</Title>
          )
        },
      },
      {
        title: 'Thể loại',
        dataIndex: "category",
        key: "category",
        ...this.getColumnSearchProps("category"),
        render: category => {
          if(!category) return (
            <span>
              <Tag color="gold">
                Không có thể loại
              </Tag>
            </span>
          )
          return (
            <span>
              {category.split(",").map(ctgr => {
                if (ctgr == 'news') return (
                  <span key={ctgr}>
                    <Tag color="#f50">
                      Tin tức
                    </Tag>
                  </span>
                )
                if (ctgr == 'events') return (
                  <span key={ctgr}>
                    <Tag color="#2db7f5">
                      Sự kiện
                    </Tag>
                  </span>
                )
                if (ctgr == 'stocks') return (
                  <span key={ctgr}>
                    <Tag color="#87d068">
                      Cổ phiếu
                    </Tag>
                  </span>
                )
              })
              }
            </span>
          )
        }
      },
      {
        title: "Chủ đề",
        dataIndex: "tags",
        key: "tags",
        ...this.getColumnSearchProps("tags"),
        render: tags => {
          if (!tags) return (
            <span>
              <Tag color="gold">
                Không có chủ đề
              </Tag>
            </span>
          )
          return (
            <span>
              {tags.split(",").map(tag => <span key={tag}>
                <Tag color="blue" style={{marginBottom: 10}}>
                  {tag}
                </Tag>
              </span>)
              }
            </span>
          )
        },
      },
      {
        title: "Trạng thái",
        dataIndex: "status",
        key: "status",
        ...this.getColumnSearchProps("status"),
        render: status => {
          let color;
          if (status === 'published') {
            color = 'green';
            return (
              <Tag color={color} key={status}>
                Xuất bản
              </Tag>
            )
          }
          else if (status === 'draft') {
            color = 'gold';
            return (
              <Tag color={color} key={status}>
                Nháp
              </Tag>
            )
          }
          else if (status === 'disable') {
            color = 'red';
            return (
              <Tag color={color} key={status}>
                Ẩn
              </Tag>
            )
          } else {
            return (
              <Tag color="gold">
                Trạng thái chưa cập nhật
              </Tag>
            )
            
          }
        },
      },
      {
        title: 'Nổi bật',
        dataIndex: 'popular',
        key: 'popular',
        ...this.getColumnSearchProps("popular"),
        render: popular => {
          let color;
          if (popular === true) {
            color = 'purple';
            return (
              <Tag color={color} key={popular}>
                Nổi bật
              </Tag>
            )
          }

        },

      },
      {
        title: 'Hành động',
        dataIndex: 'key',
        key: 'key',
        width: '170px',
        render: (record) =>
          this.state.dataSource.length >= 1 ? (
            <div>
              <Link to={'/viewPost?id=' + record}>
                <span>Xem</span>
              </Link>
              <Divider type="vertical" />
              <Popconfirm title="Xác nhận xóa?" okText="Có" cancelText="Hủy" onConfirm={() => this.handleDelete(record)}>
                <a href="# ">Xóa</a>
              </Popconfirm>
              <Divider type="vertical" />
              <Link to={'/editorPage?id=' + record}>
                <span>Sửa</span>
              </Link>
            </div>

          ) : null,
      },
      
    ];
    if(this.state.isLoading){
      return <div className="loadData">
                <Spin size="large" tip="Loading..." />
              </div>
    }
    return (
      <>
        <Table columns={columns} dataSource={this.state.dataSource} bordered />
      </>
    );
  }
}
