import React from 'react';
import { Form, Icon, Input, Button, } from 'antd';
import { Link } from "react-router-dom";
import {withFirebase} from '../utils/Firebase';


const INITIAL_STATE = {
    email: '',
    password: '',
    error: null,
  };
  
  const ERROR_CODE_ACCOUNT_EXISTS =
    'auth/account-exists-with-different-credential';
  
  const ERROR_MSG_ACCOUNT_EXISTS = `
    An account with an E-Mail address to
    this social account already exists. Try to login from
    this account instead and associate your social accounts on
    your personal account page.
  `;
class Login extends React.Component {
    constructor(props) {
        super(props);
    
        this.state = { ...INITIAL_STATE };
      }
    
      onSubmit = event => {
        event.preventDefault();
        const { email, password } = this.state;
    
        this.props.firebase
          .doSignInWithEmailAndPassword(email, password)
          .then(() => { console.log('login susscess');
            this.setState({ ...INITIAL_STATE });
            // this.props.history.push(ROUTES.HOME);
          })
          .catch(error => {
            console.log('login failer', error);
            this.setState({ error }); 
          });
    
      };
    
      onChange = event => {
        this.setState({ [event.target.name]: event.target.value });
      };
    
    render() {
        const { email, password, error } = this.state;

        const isInvalid = password === '' || email === '';
        return (
            <>
                <Form onSubmit={this.onSubmit} className="login-form">
                    <Form.Item>
                        <Input
                            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            name="email"
                            value={email}
                            onChange={this.onChange}
                            type="text"
                            placeholder="Địa chỉ email"
                        />
                    </Form.Item>
                    <Form.Item>
                        <Input
                            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            name="password"
                            value={password}
                            onChange={this.onChange}
                            type="password"
                            placeholder="Mật Khẩu"
                        />
                    </Form.Item>
                    <Form.Item>
                        {/* <Checkbox>Remember me</Checkbox> */}
                        
                        <Button type="primary" htmlType="submit" className="login-form-button" disabled={isInvalid} >
                            Log in
                        </Button>
                        {error && <p>{error.message}</p>}
                        <Link to={'/register'}>Đăng ký</Link>
                        <Link to={'/passwordForget'} className="login-form-forgot">Quên mật khẩu</Link>
                    </Form.Item>
                </Form>
            </>
        );
    }
}

export default withFirebase(Login);