import React, {useState, useEffect} from "react";
import { Card, Select, Button, notification, Icon, Input, Row, Col, Form, Switch} from "antd";
import EmailEditor from 'react-email-editor';
import firebase from "firebase";
import UploadImg from "../components/UploadImg";
import FormItem from "antd/lib/form/FormItem";

const { TextArea } = Input;
const { Option } = Select;
const formItemLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 16 },
};

export default class EditorPage extends React.Component {
    state = {
        loading: false,
        iconLoading: false,
        postName: '',
        postTags: '',
        description: '',
        link: '',
        category: undefined,
        popular: false,
        countView: 0
    };
    componentWillReceiveProps(nextProps){
        const postId = nextProps.history.location.search.slice(4);
        if(!postId && nextProps.history.location.pathname == '/editorPage'){
            
            this.setState(
                {
                    loading: false,
                    iconLoading: false,
                    postName: '',
                    postTags: '',
                    description: '',
                    link: '',
                    category: undefined,
                    postId: '',
                    postImg: '',
                    status: undefined,
                    popular: false,
                    
                }          
            )
            console.log('run')
        
        }
       
    }
    componentDidMount(){
        this.postId = this.props.history.location.search.slice(4);
        this.userId = firebase.auth().currentUser.uid;
        this.setState({
            postId : this.postId,
            countView : this.countView,
        })
        console.log('Edit id',this.postId);
        // if(this.postId){
        //     this.countView+=1;
        //     console.log('vieews', this.countView)
        // }
        firebase.database().ref('posts').once('value',snapshot=>{
            const data = [];
            snapshot.forEach((childSnapshot) =>{
                if(childSnapshot.key  === this.postId){
                    const data = childSnapshot.val();
                    
                    // console.log('Edit post',data.content);
                    this.editor.loadDesign(JSON.parse(data.content));
                    this.setState({postName: data.title,
                        postTags: data.tags,
                        description: data.description,
                        link: data.link,
                        category: data.category,
                        status: data.status,
                        popular: data.popular,
                        countView: data.countView});
                        
                        
                    if(this.uploadImgView){
                        this.uploadImgView.updateLink(data.link);
                    }
                }
                
              data.push({
                ...childSnapshot.val(),
                key: childSnapshot.key});
              // ...
            });
          });
        
    }
    
    enterLoading = () => {
        this.setState({ loading: true });
    };

    save = () => {
        if(this.state.postName === '' || this.state.postTags === '' || this.state.description === '' || this.state.link === '' || this.state.category === ''|| this.state.popular === '' || this.state.status === ''){
            notification.open({
                message: 'Vui lòng nhập đầy đủ thông tin!',        
                icon: <Icon type="warning" style={{ color: '#FFC107' }} />,
              });
              return;
        }
        
        if(this.editor){
            this.editor.exportHtml(data=>{
                const { design, html } = data
                // console.log('exportHtml', html)
                // console.log('Save data',data);

                if(this.postId){
                    const email = firebase.auth().currentUser.email;
                    const urlImg = firebase.storage().ref();
                    // console.log('user id',this.userId);
           
                    firebase.database().ref('posts/' + this.postId ).update({
                        content: JSON.stringify(design), 
                        title: this.state.postName, 
                        tags: this.state.postTags,
                        date: new Date().getTime(),   
                        author: email,
                        contentAsHTML: html,
                        description: this.state.description,
                        link: this.state.link,
                        category: this.state.category,
                        status:  this.state.status,
                        popular: this.state.popular,
                    })
                    
                } else{
                    // console.log('user id',this.userId);
                    const email = firebase.auth().currentUser.email;
                    const urlImg = firebase.storage().ref();
                    
                    firebase.database().ref('posts').push({
                        date: new Date().getTime(),                       
                        title: this.state.postName,                        
                        author: email,
                        tags: this.state.postTags,
                        content: JSON.stringify(design),
                        contentAsHTML: html,
                        description: this.state.description,
                        link: this.state.link,
                        category: this.state.category,
                        status: this.state.status,
                        popular: this.state.popular,
                        countView: 0,
                    });
                    
                }
                notification.open({
                    message: 'Bài viết đã được lưu lại',        
                    icon: <Icon type="smile" style={{ color: '#108ee9' }} />,
                });
            });

        }
        
    };

    // onChangeCategory(value) {
    //     console.log(`selected ${value}`);
    // }
    onChangeCategory = value => {
        this.setState({category: value});
        console.log(`selected`, value)
    }
    onChangeStatus = value => {
        this.setState({status: value});
        console.log(`status is`, value)
    }
    onChangePopular = checked => {
        this.setState({popular: checked});
        console.log(`checked switch`, checked)
    }
    onBlur() {
        // console.log("blur");
    }
    onFocus() {
        // console.log("focus");
    }
    onSearch(val) {
        console.log("search:", val);
    }
    onChangeName = event => {
        this.setState({postName:event.target.value});
    };
    onChangeTags = event => {
        this.setState({postTags: event.target.value});
    }
    onChangeDes = event => {
        this.setState({description: event.target.value});
    }
    onUploadSuccess = link => {
        console.log('ddaay la link: ' ,link);
        this.setState({link});
    }
   
    render() {
        
        return (          
            <>
                <Card
                    title={this.state.postId? "Sửa Tin Tức" : "Thêm Tin Tức"} 
                    bordered={false}
                    extra={
                        <div>
                            
                            <Button
                                type="primary"
                                className="bg-dark-blue "
                                loading={this.state.loading}
                                onClick={this.save}
                                
                            >
                                Lưu Lại
                            </Button>
                            
                        </div>
                    }
                >
                    <Row gutter={[{ xs: 8, sm: 16, md: 24, lg: 32 }, 20]}>
                        <Col className="gutter-row" span={12}>
                            <Form.Item className="form-item" {...formItemLayout} label="Tên Bài Viết: ">
                                <TextArea placeholder="Nhập tên bài viết"
                                value={this.state.postName}
                                onChange={this.onChangeName}                                                
                                autoSize />
                            </Form.Item>
                            <Form.Item className="form-item" {...formItemLayout} label="Mô Tả: ">
                                <TextArea
                                placeholder="Nhập mô tả bài viết"
                                autoSize={{ minRows: 6, maxRows: 8 }}
                                value={this.state.description}
                                onChange={this.onChangeDes}
                                />
                            </Form.Item>
                            
                            <Form.Item className="form-item" {...formItemLayout} label="Tên Chủ Đề: ">
                                <TextArea placeholder="Nhập tên chủ đề"
                                value={this.state.postTags}
                                onChange={this.onChangeTags}    
                                                   
                                autoSize />
                            </Form.Item>
                            <FormItem className="form-item" {...formItemLayout} label="Bài viết nổi bật: ">
                                <Switch
                                    checkedChildren={<Icon type="check" />}
                                    unCheckedChildren={<Icon type="close" />}
                                    defaultChecked
                                    onChange={this.onChangePopular}
                                    checked={this.state.popular}
                                />
                            </FormItem>
                                                      
                        </Col>
                        <Col className="gutter-row" span={12}>
                            <Form.Item className="form-item" {...formItemLayout} label="Thể loại: ">
                                <Select
                                    showSearch
                                    style={{ width: 200 }}
                                    placeholder="Chọn thể loại"
                                    optionFilterProp="children"
                                    onChange={this.onChangeCategory}
                                    value={this.state.category}
                                    // value={this.category}
                                    onFocus={this.onFocus}
                                    onBlur={this.onBlur}
                                    onSearch={this.onSearch}
                                    filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                >
                                    <Option value="news">Tin tức</Option>
                                    <Option value="events">Sự kiện</Option>
                                    <Option value="stocks">Cổ phiếu</Option>
                                </Select>
                            </Form.Item>
                            <Form.Item className="form-item" {...formItemLayout} label="Trạng thái: ">
                                <Select
                                    showSearch
                                    style={{ width: 200 }}
                                    placeholder="Chọn trạng thái của bài viết"
                                    optionFilterProp="children"
                                    onChange={this.onChangeStatus}
                                    value={this.state.status}
                                    // value={this.category}
                                    onFocus={this.onFocus}
                                    onBlur={this.onBlur}
                                    onSearch={this.onSearch}
                                    filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                >
                                    <Option value="published">Xuất bản</Option>
                                    <Option value="draft">Nháp</Option>
                                    <Option value="disable">Ẩn</Option>
                                </Select>
                            </Form.Item>
                            <UploadImg key={this.state.postImg} onUploadSuccess={this.onUploadSuccess}
                            ref={ref => this.uploadImgView= ref} />
                            
                        </Col>
                        <Col className="gutter-row" span={24}>
                            <hr></hr>
                            <EmailEditor
                            key={this.state.postId}
                            style={{width: '2000px'}}
                            ref={ref=>this.editor = ref} />
                        </Col>
                    </Row>
                    
                </Card>
            </>
        );
    }

}
